.contact {
    height: 100%;
    background-color: #8e45ae;
    padding-top: 2rem;

    &__header {
        color: white;
        text-align: center;

        &--first {
            font-size: 2rem;
        }
        
        &--second {
            font-size: 1.5rem;
            font-weight: 700;

            &:after {
                display: block;
                margin: 1rem auto;
                height: .25rem;
                width: 2rem;
                background: white;
                content: '';
            }
        }
    }

    &__information {
        margin: 5rem 0;
        display: flex;
        justify-content: space-evenly;

        &__detail {
            margin-right: 4rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            color: white;
        }

        &__logo {
            height: 1.5rem;
        }

        &__header {
            font-size: 1.25rem;
        }

        &__info {
            font-size: 1rem;
        }
    }

    &__social-container {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    &__footer {
        color: white;
        text-align: center;
        font-size: 1rem;
        margin-top: 2rem;
    }
}